      }],
      requestActions: [{
        text: "Pending",
        value: "pending"
      }, {
        text: "Approve",
        value: "approved"
      }, {
        text: "Reject",
        value: "rejected"
      }],
      requestAction: "",
      extensionDate: "",
      extensionDateFormatted: "",
      extensionDateMenu: false,
      extensionReason: "",
      totalSteps: 4
    }
  },
  computed: {
    canExtendDays() {
      if(this.specialStudyExtensionRequest.id && this.specialStudyExtensionRequest.id !== 'loading') {
        return false
      }
      if(this.leaveRequest.categoryid === 'specialstudy' && this.leaveRequest.statuscode === 'approved') {
        let role = this.$store.state.user.obj.resource.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-assign-role"
        })
        if(role && role.valueReference.reference === "Basic/na-hr-manager") {
          return true
        }
      }
      return false
    },
    firstLevelApprQuestionnaire() {
      if(this.leaveRequest.categoryid === 'specialstudy') {
        return '/questionnaire/hr-leave-approval/hr-leave-approval?practitioner=' + this.practitionerId + '&leaverequest=' + this.queries.id
      }
      return '/questionnaire/supervisor-leave-approval/supervisor-leave-approval?practitioner=' + this.practitionerId + '&leaverequest=' + this.queries.id
    },
    secondLevelApprQuestionnaire() {
      if(this.leaveRequest.categoryid === 'specialstudy') {
        return '/questionnaire/hr-supervisor-leave-approval/hr-supervisor-leave-approval?practitioner=' + this.practitionerId + '&leaverequest=' + this.queries.id
      }
      return '/questionnaire/supervisor-supervisor-leave-approval/supervisor-supervisor-leave-approval?practitioner=' + this.practitionerId + '&leaverequest=' + this.queries.id
    },
    thirdLevelApprQuestionnaire() {
      return '/questionnaire/hr-leave-approval/hr-leave-approval?practitioner=' + this.practitionerId + '&leaverequest=' + this.queries.id
    },
    fourthLevelApprQuestionnaire() {
      return '/questionnaire/ed-leave-approval/ed-leave-approval?practitioner=' + this.practitionerId + '&leaverequest=' + this.queries.id
    },
    canAddFirstLevelApproval() {
      if(this.firstLevelApproval.id) {
        return false
      }
      if(this.leaveRequest.categoryid === 'specialstudy') {
        let role = this.$store.state.user.obj.resource.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-assign-role"
        })
        if(role && role.valueReference.reference === "Basic/na-hr-manager") {
          return true
        }
      } else {
        let userPractitioner = this.$store.state.user.obj.resource.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
        })
        if(userPractitioner && userPractitioner.valueReference.reference === this.supervisor) {
          return true
        }
      }
      return false
    },
    canAddSecondLevelApproval() {
      if(!this.firstLevelApproval.id || this.firstLevelApproval.id === 'loading' || this.firstLevelApproval.statuscode === 'rejected' || this.secondLevelApproval.id) {
        return false
      }
      let userPractitioner = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
      })
      if(!userPractitioner) {
        console.error('User not associated with any practitioner');
        return false
      }
      if(this.leaveRequest.categoryid === 'specialstudy') {
        if(userPractitioner.valueReference.reference === this.HRSupervisor) {
          return true
        }
      } else {
        if(userPractitioner.valueReference.reference === this.supervisorSupervisor) {
          return true
        }
      }
      return false
    },
    canAddThirdLevelApproval() {
      if(!this.secondLevelApproval.id || this.secondLevelApproval.id === 'loading' || this.secondLevelApproval.statuscode === 'rejected' || this.thirdLevelApproval.id) {
        return false
      }
      if(this.leaveRequest.categoryid === 'specialstudy') {
        return false
      }
      let role = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-assign-role"
      })
      if(role && role.valueReference.reference === "Basic/na-hr-manager" && this.secondLevelApproval.id && !this.thirdLevelApproval.id) {
        return true
      }
      return false
    },
    canAddFourthLevelApproval() {
      if(!this.firstLevelApproval.id || this.firstLevelApproval.id === 'loading' || !this.secondLevelApproval.id || this.secondLevelApproval.id === 'loading' || this.secondLevelApproval.statuscode === 'rejected'|| this.thirdLevelApproval.id === 'loading' || this.thirdLevelApproval.statuscode === 'rejected' || this.fourthLevelApproval.id) {
        return false
      }
      if(this.leaveRequest.categoryid !== 'unpaidauthorized' || (this.leaveRequest.categoryid === 'unpaidauthorized' && this.leaveRequest.days < 90)) {
        return false
      }
      let role = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-assign-role"
      })
      if(role && role.valueReference.reference === "Basic/na-executive-director") {
        return true
      }
      return false
    },
    canRequestCarryForward() {
      if((this.carryForwardRequest.id && this.carryForwardRequest.id !== 'loading') || this.carryForwardRequest.id === 'loading') {
        return false
      }
      if(this.leaveDetails.length) {
        let startPeriod = this.annualLeave.currentPeriod.split("/")[0]
        let months = this.$moment().diff(startPeriod, 'months')
        if(months > 6 || this.annualLeave.balanceCarried <= 0 || this.annualLeave.balanceCarried <= this.annualLeave.expired) {
          return false
        } else if(this.annualLeave.maxDays) {
          return true
        }
      }
      return false
    },
    canApproveRequestCarryForward() {
      if(!this.carryForwardRequest.id || this.carryForwardRequest.id === 'loading') {
        return false
      }
      let role = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-assign-role"
      })
      if(role && role.valueReference.reference === "Basic/exec-manager" && this.carryForwardRequest.status.code !== 'approved') {
        return true
      }
      return false
    },
    canApproveRequestSpecialStudyExt() {
      if(!this.specialStudyExtensionRequest.id || this.specialStudyExtensionRequest.id === 'loading') {
        return false
      }
      if(this.specialStudyExtensionRequest.status.code === 'approved') {
        return false
      }
      let userPractitioner = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
      })
      if(!userPractitioner) {
        console.error('User not associated with any practitioner');
        return false
      }
      if(userPractitioner.valueReference.reference === this.HRSupervisor) {
        return true
      }
      return false
    }
  },
  methods: {
    getLeaveCaryForward() {
      return new Promise((resolve) => {
        let query = {
          practitioner: "Practitioner/" + this.practitionerId,
          _profile: "http://ihris.org/fhir/StructureDefinition/leave-carry-forward-request-profile",
          toleaveperiod: this.annualLeave.currentPeriod
        }
        fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then(async(response) => {
          if(response && response.entry && response.entry.length) {
            this.populateCarryForward(response.entry[0].resource)
          } else {
            this.carryForwardRequest.id = ""
          }
          resolve()
        }))
      })
    },
    populateCarryForward(carry) {
      this.carryForwardRequest.id = carry.id
      this.carryForwardRequest.fromPeriod = carry.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/from-leave-period"
      }).valueString
      this.carryForwardRequest.toPeriod = carry.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/to-leave-period"
      }).valueString
      this.carryForwardRequest.requestDate = carry.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/request-date"
      }).valueDate
      this.carryForwardRequest.status = carry.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/carry-approval-status"
      }).valueCoding
    },
    getSpecialStudyExtensionRequest() {
      return new Promise((resolve) => {
        let query = {
          practitioner: "Practitioner/" + this.practitionerId,
          _profile: "http://ihris.org/fhir/StructureDefinition/special-study-extension-request-profile",
          leaverequest: "Basic/" + this.queries.id
        }
        fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then(async(response) => {
          if(response && response.entry && response.entry.length) {
            let user = response.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/requested-by"
            })
            if(user) {
              await this.getHRSupervisor(user.valueReference.reference)
            }
            this.populateSpecialStudyExtensionReq(response.entry[0].resource)
          } else {
            this.specialStudyExtensionRequest.id = ""
          }
          resolve()
        }))
      })
    },
    populateSpecialStudyExtensionReq(extension) {
      this.specialStudyExtensionRequest.id = extension.id
      this.specialStudyExtensionRequest.fromDate = extension.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/from-date"
      }).valueDate
      this.specialStudyExtensionRequest.toDate = extension.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/to-date"
      }).valueDate
      this.specialStudyExtensionRequest.requestDate = extension.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/request-date"
      }).valueDate
      this.specialStudyExtensionRequest.status = extension.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/special-study-extension-approval-status"
      }).valueCoding
      this.specialStudyExtensionRequest.description = extension.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/description"
      }).valueString
      let requestor = extension.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/requested-by"
      })
      if(requestor) {
        this.specialStudyExtensionRequest.requestor.id = requestor.valueReference.reference
        fetch("/site/leave/leaveProcessor/" + requestor.valueReference.reference.split("/")[1]).then(resp => resp.json().then((response) => {
          this.specialStudyExtensionRequest.requestor.name = response.name
        }))
      }
      let processor = extension.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/processed-by"
      })
      if(processor) {
        this.specialStudyExtensionRequest.processor.id = processor.valueReference.reference
        fetch("/site/leave/leaveProcessor/" + processor.valueReference.reference.split("/")[1]).then(resp => resp.json().then((response) => {
          this.specialStudyExtensionRequest.processor.name = response.name
        }))
      }
    },
    getLeaveDetails() {
      return new Promise((resolve) => {
        fetch("/site/leave/balance/" + this.practitionerId).then(response => response.json().then(async(response) => {
          this.leaveDetails = response
          if(this.leaveRequest.categoryid === 'annual') {
            this.annualLeave = this.leaveDetails.find((leave) => {
              return leave.categoryid === "annual"
            })
            await this.getLeaveCaryForward()
            resolve()
          } else if(this.leaveRequest.categoryid === 'specialstudy') {
            await this.getSpecialStudyExtensionRequest()
            resolve()
          } else {
            resolve()
          }
        }))
      })
    },
    getSupervisor() {
      return new Promise((resolve) => {
        fetch("/site/leave/getSupervisor/" + this.practitionerId + "?startDate=" + this.leaveRequest.startDate).then(response => response.json().then(async(response) => {
          if(response.supervisor) {
            this.supervisor = response.supervisor
            // get supervisor of supervisor
            fetch("/site/leave/getSupervisor/" + this.supervisor.split("/")[1] + "?startDate=" + this.leaveRequest.startDate).then(response => response.json().then(async(response) => {
              if(response.supervisor) {
                this.supervisorSupervisor = response.supervisor
              } else {
                this.noSupervisorSupervisor = true
              }
              resolve()
            }))
          } else {
            this.noSupervisor = true
            resolve()
          }
        }))
      })
    },
    getHRSupervisor(hr) {
      return new Promise((resolve) => {
        fetch("/site/leave/getUserSupervisor/" + hr.split("/")[1]).then(response => response.json().then((response) => {
          if(!response.supervisor) {
            this.noHRSupervisor = true
          } else {
            this.HRSupervisor = response.supervisor
          }
          resolve()
        }))
      })
    },
    populateCarry() {
      this.overlayUnclosed = true
      fetch("/site/leave/populateCarry/" + this.practitionerId).then(() => {
        Promise.all([this.getLeaveDetails(), this.getSupervisor(), this.getFirstLevelApproval()]).then(() => {
          this.overlayUnclosed = false
        })
      })
    },
    getLeave() {
      fetch("/fhir/Basic/" + this.queries.id).then(response => response.json().then(async(leave) => {
        this.practitionerId = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
        }).valueReference.reference
        this.practitionerId = this.practitionerId.split("/")[1]
        this.leaveRequest.id = this.queries.id
        this.leaveRequest.startDate = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/start-date"
        }).valueDate
        this.leaveRequest.endDate = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/end-date"
        }).valueDate
        this.leaveRequest.returnDate = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/return-date"
        }).valueDate
        this.leaveRequest.days = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/leave-days"
        })?.valueInteger
        this.leaveRequest.status = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/leave-status"
        }).valueCoding.display
        this.leaveRequest.statuscode = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/leave-status"
        }).valueCoding.code
        let category = leave.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/leave-category"
        }).valueReference.reference
        this.leaveRequest.categoryid = category.split("/")[1]
        await fetch("/fhir/" + category).then(response => response.json().then(async(category) => {
          this.leaveRequest.category = category.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-basic-name"
          }).valueString
        }))
        if(this.leaveRequest.categoryid === 'unpaidauthorized' && this.leaveRequest.days >= 90) {
          this.totalSteps = 5
        }
        if(this.leaveRequest.status === "Approved") {
          this.step = this.totalSteps
        }
        this.populateCarry()
      }))
    },
    getFirstLevelApproval() {
      return new Promise((resolve) => {
        let query = {
          leaverequest: 'Basic/' + this.queries.id,
          _sort: '_lastUpdated'
        }
        let statusURL
        if(this.leaveRequest.categoryid === 'specialstudy') {
          this.firstLevelApproval.title = "HR Approval"
          query._profile = "http://ihris.org/fhir/StructureDefinition/hr-leave-approval-profile"
          statusURL = "http://ihris.org/fhir/StructureDefinition/hr-leave-status"
        } else {
          this.firstLevelApproval.title = "Immediate Supervisor Approval"
          query._profile = "http://ihris.org/fhir/StructureDefinition/supervisor-leave-approval-profile"
          statusURL = "http://ihris.org/fhir/StructureDefinition/supervisor-leave-status"
        }
        fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then(async(appr) => {
          if(appr.entry && appr.entry.length) {
            if(this.step != this.totalSteps) {
              this.step=2
            }
            this.firstLevelApproval.id = appr.entry[0].resource.id
            this.firstLevelApproval.status = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === statusURL
            }).valueCoding.display
            this.firstLevelApproval.statuscode = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === statusURL
            }).valueCoding.code
            this.firstLevelApproval.date = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/action-date"
            }).valueDate
            let description = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/description"
            })
            if(description) {
              this.firstLevelApproval.description = description.valueString
            }
            let user = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/user-reference"
            })
            if(user) {
              this.firstLevelApproval.user.id = user.valueReference.reference
              fetch("/site/leave/leaveProcessor/" + user.valueReference.reference.split("/")[1]).then(resp => resp.json().then((response) => {
                this.firstLevelApproval.user.name = response.name
              }))
              if(this.leaveRequest.categoryid === 'specialstudy') {
                await this.getHRSupervisor(user.valueReference.reference)
              }
            }
            await this.getSecondLevelApproval()
            resolve()
          } else {
            this.firstLevelApproval.id = ""
            this.secondLevelApproval.id = ""
            this.thirdLevelApproval.id = ""
            this.fourthLevelApproval.id = ""
            resolve()
          }
        }))
      })
    },
    getSecondLevelApproval() {
      return new Promise((resolve) => {
        let query = {
          leaverequest: 'Basic/' + this.queries.id,
          _sort: '_lastUpdated'
        }
        let statusURL
        if(this.leaveRequest.categoryid === 'specialstudy') {
          this.secondLevelApproval.title = "HR Supervisor Approval"
          query._profile = "http://ihris.org/fhir/StructureDefinition/hr-supervisor-leave-approval-profile"
          statusURL = "http://ihris.org/fhir/StructureDefinition/hr-supervisor-leave-status"
        } else {
          this.secondLevelApproval.title = "Supervisor's Supervisor Approval"
          query._profile = "http://ihris.org/fhir/StructureDefinition/supervisor-supervisor-leave-approval-profile"
          statusURL = "http://ihris.org/fhir/StructureDefinition/supervisor-supervisor-leave-status"
        }
        fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then(async(appr) => {
          if(appr.entry && appr.entry.length) {
            if(this.step != this.totalSteps) {
              this.step=3
            }
            this.secondLevelApproval.id = appr.entry[0].resource.id
            this.secondLevelApproval.status = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === statusURL
            }).valueCoding.display
            this.secondLevelApproval.statuscode = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === statusURL
            }).valueCoding.code
            this.secondLevelApproval.date = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/action-date"
            }).valueDate
            let description = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/description"
            })
            if(description) {
              this.secondLevelApproval.description = description.valueString
            }
            let user = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/user-reference"
            })
            if(user) {
              this.secondLevelApproval.user.id = user.valueReference.reference
              fetch("/site/leave/leaveProcessor/" + user.valueReference.reference.split("/")[1]).then(resp => resp.json().then((response) => {
                this.secondLevelApproval.user.name = response.name
              }))
            }
            await this.getThirdLevelApproval()
            resolve()
          } else {
            this.secondLevelApproval.id = ""
            this.thirdLevelApproval.id = ""
            this.fourthLevelApproval.id = ""
            resolve()
          }
        }))
      })
    },
    getThirdLevelApproval() {
      return new Promise((resolve) => {
        if(this.leaveRequest.categoryid === 'specialstudy') {
          this.thirdLevelApproval.id = ""
          return resolve()
        }
        this.thirdLevelApproval.title = "HR Approval"
        let query = {
          _profile: 'http://ihris.org/fhir/StructureDefinition/hr-leave-approval-profile',
          leaverequest: 'Basic/' + this.queries.id,
          _sort: '_lastUpdated'
        }
        fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then(async(appr) => {
          if(appr.entry && appr.entry.length) {
            if(this.step != this.totalSteps) {
              this.step=4
            }
            this.thirdLevelApproval.id = appr.entry[0].resource.id
            this.thirdLevelApproval.status = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/hr-leave-status"
            }).valueCoding.display
            this.thirdLevelApproval.statuscode = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/hr-leave-status"
            }).valueCoding.code
            this.thirdLevelApproval.date = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/action-date"
            }).valueDate
            let description = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/description"
            })
            if(description) {
              this.thirdLevelApproval.description = description.valueString
            }
            let user = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/user-reference"
            })
            if(user) {
              this.thirdLevelApproval.user.id = user.valueReference.reference
              fetch("/site/leave/leaveProcessor/" + user.valueReference.reference.split("/")[1]).then(resp => resp.json().then((response) => {
                this.thirdLevelApproval.user.name = response.name
              }))
            }
            await this.getFourthLevelApproval()
            resolve()
          } else {
            this.thirdLevelApproval.id = ""
            this.fourthLevelApproval.id = ""
            resolve()
          }
        }))
      })
    },
    getFourthLevelApproval() {
      return new Promise((resolve) => {
        if(this.leaveRequest.categoryid !== 'unpaidauthorized' || (this.leaveRequest.categoryid == 'unpaidauthorized' && this.leaveRequest.days < 90)) {
          this.fourthLevelApproval.id = ""
          return resolve()
        }
        this.fourthLevelApproval.title = "ED Approval"
        let query = {
          _profile: 'http://ihris.org/fhir/StructureDefinition/ed-leave-approval-profile',
          leaverequest: 'Basic/' + this.queries.id,
          _sort: '_lastUpdated'
        }
        fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then(async(appr) => {
          if(appr.entry && appr.entry.length) {
            this.fourthLevelApproval.id = appr.entry[0].resource.id
            this.fourthLevelApproval.status = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/ed-leave-status"
            }).valueCoding.display
            this.fourthLevelApproval.statuscode = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/ed-leave-status"
            }).valueCoding.code
            this.fourthLevelApproval.date = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/action-date"
            }).valueDate
            let description = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/description"
            })
            if(description) {
              this.fourthLevelApproval.description = description.valueString
            }
            let user = appr.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/user-reference"
            })
            if(user) {
              this.fourthLevelApproval.user.id = user.valueReference.reference
              fetch("/site/leave/leaveProcessor/" + user.valueReference.reference.split("/")[1]).then(resp => resp.json().then((response) => {
                this.fourthLevelApproval.user.name = response.name
                resolve()
              }))
            } else {
              resolve()
            }
          } else {
            this.fourthLevelApproval.id = ""
            resolve()
          }
        }))
      })
    },
    createCarryForwardRequest() {
      this.overlayUnclosed = true;
      fetch("/site/leave/createCarryRequest/" + this.practitionerId, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(this.annualLeave),
      }).then((response) => {
        this.overlayUnclosed = false;
        this.carryForwardDialog = false
        if(response.status === 201) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Carry Forward Request Created Successfully",
          });
          response.json().then((response) => {
            this.getLeave()
            this.populateCarryForward(response)
          })
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed To Create Carry Forward Request."
          });
        }
      }).catch(() => {
        this.overlayUnclosed = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed To Create Carry Forward Request."
        });
      })
    },
    createCarryForwardAction() {
      this.overlayUnclosed = true;
      fetch("/site/leave/createCarryAction/" + this.carryForwardRequest.id + "/" + this.requestAction, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual"
      }).then((response) => {
        this.requestAction = ""
        this.overlayUnclosed = false;
        this.carryForwardActionDialog = false
        if(response.status === 200) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Carry Forward Action Added Successfully",
          });
          response.json().then((response) => {
            this.populateCarryForward(response)
            this.getLeaveDetails()
          })
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed To Add Carry Forward Action."
          });
        }
      }).catch(() => {
        this.overlayUnclosed = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed To Add Carry Forward Action."
        });
      })
    },
    createSpecialStudyExtensionReq() {
      this.overlayUnclosed = true;
      let leaveRequest = JSON.parse(JSON.stringify(this.leaveRequest))
      leaveRequest.extensionDate = this.extensionDate
      leaveRequest.extensionReason = this.extensionReason
      fetch("/site/leave/createSpecialStudyExtensionRequest/" + this.practitionerId, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(leaveRequest),
      }).then((response) => {
        this.overlayUnclosed = false;
        this.specialStudyExtendDialog = false
        if(response.status === 201) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Extension Request Created Successfully",
          });
          response.json().then((response) => {
            this.getLeave()
            this.populateSpecialStudyExtensionReq(response)
          })
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed To Create Extension Request."
          });
        }
      }).catch(() => {
        this.overlayUnclosed = false;
        this.specialStudyExtendDialog = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed To Create Carry Forward Request."
        });
      })
    },
    createSpecialStudyExtensionAction() {
      this.overlayUnclosed = true;
      fetch("/site/leave/createSpecialStudyExtensionAction/" + this.specialStudyExtensionRequest.id + "/" + this.requestAction, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual"
      }).then((response) => {
        this.requestAction = ""
        this.overlayUnclosed = false;
        this.specialStudyExtensionActionDialog = false
        if(response.status === 200) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Carry Forward Action Added Successfully",
          });
          response.json().then(() => {
            this.getLeaveDetails()
          })
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed To Add Carry Forward Action."
          });
        }
      }).catch(() => {
        this.overlayUnclosed = false;
        this.specialStudyExtensionActionDialog = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed To Add Carry Forward Action."
        });
      })
    }
  },
  created() {
    this.getLeave()
  },
  components: {
    "leave-request-stepper": () => import("./LeaveRequestStepper"),
    "special-study-leave-request-stepper": () => import("./SpecialStudyLeaveRequestStepper"),
  }
}
</script>